import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import { Select } from "antd";
import toast from "react-hot-toast";
import axios from "axios";
import { useNavigate } from "react-router-dom";
const { Option } = Select;

const Createproduct = () => {
  const [categories, Setcategories] = useState([]);
  const [photo, Setphoto] = useState("");
  const [productName, SetproductName] = useState("");
  const [description, Setdescription] = useState("");
  const [category, Setcategory] = useState("");
  const [quantity, Setquantity] = useState("");
  const [price, Setprice] = useState("");
  const [shipping, Setshipping] = useState("");
  const Navigate = useNavigate()
  
  const HandleCreate = async (e) => {
    e.preventDefault();
    try {
      
      const productData = new FormData();
      productData.append("productName", productName);
      productData.append("description", description);
      productData.append("category", category);
      productData.append("quantity", quantity);
      productData.append("price", price);
      productData.append("shipping", shipping);
      productData.append("photo", photo);

      const { data } = await axios.post(
        "/api/admin/product/create-product",
        productData
      );
      if (data?.success) {
        toast.success("product created successfully");
        Navigate('/profile/admin/products')
      } else {
        toast.error(data.error);
      }
    } catch (error) {
     
      toast.error("something went wrong");
    }
  };

  const getCategories = async () => {
    try {
      const { data } = await axios.get("/api/admin/category/get-category");
      if (data?.success) {
        Setcategories(data?.categories);
        toast.success("categories accquired successfully");
      }
    } catch (error) {
      (error);
      toast.error("cannot get categories");
    }
  };
  useEffect(() => {
    getCategories();
  }, []);
  return (
    <div>
      <Nav />
      <div className="createProduct_main p-2">
        <hr />
        <div className="d-flex row container-fluid  ">
          <div className="col-lg-2 col-sm-12">
            <AdminMenu />
          </div>
          <div className="col-lg-10 col-sm-12 ps-1  ">
            <div className=" px-3 w-80  ">
           
              <h1 className="mb-3 fw-bold text-uppercase  font-monospace">Create products</h1>
             
              <div className="m-1 w-80">
                <Select
                  bordered={false}
                  placeholder="choose a category"
                  size="large"
                  showSearch
                  value={category ? category : "choose a category sir!"}
                  className="form-select mb-3"
                  onChange={(value) => {
                    Setcategory(value);
                  }}
                >
                  {categories?.map((c) => (
                    <Option key={c._id} value={c._id}>
                      {c.name}
                    </Option>
                  ))}
                </Select>
                <div className="mb-3">
                  <label className="btn btn-outline-primary  py-3 col-md-12">
                    {photo ? photo.name : "upload photo"}
                    <input
                      type="file"
                      name="photo"
                      accept="image/*"
                      className="form-control  "
                      onChange={(e) => Setphoto(e.target.files[0])}
                      hidden
                    />
                  </label>
                </div>
                <div className="mb-3">
                  {photo && (
                    <div className="text-center">
                      <img
                        src={URL.createObjectURL(photo)}
                        alt=" product"
                        height={"200px"}
                        className="img img-responsive rounded-3"
                      />
                    </div>
                  )}
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={productName}
                    className="form-control py-3" 
                    placeholder=" Product's Name"
                    onChange={(e) => {
                      SetproductName(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={description}
                    className="form-control py-3"
                    placeholder="About the product"
                    onChange={(e) => {
                      Setdescription(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    value={quantity}
                    className="form-control py-3"
                    placeholder="No  of Quantity"
                    onChange={(e) => {
                      Setquantity(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    value={price}
                    className="form-control py-3"
                    placeholder="Price in rupees"
                    onChange={(e) => {
                      Setprice(e.target.value);
                    }}
                  />
                </div>

                <Select
                  bordered={false}
                  placeholder="select shipping"
                  size="large"
                  showSearch
                  value={shipping}
                  className="form-select mb-3 "
                  onChange={(value) => {
                    Setshipping(value);
                  }}
                >
                  <Option value="0"> no</Option>
                  <Option value="1">yes</Option>
                </Select>
                <div className="mb-3">
                  <button
                    type="submit"
                    className="btn btn-primary"
                    onClick={HandleCreate}
                  >
                    CREATE PRODUCT
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Createproduct;
