import React from "react";
import { NavLink } from "react-router-dom";
const AdminMenu = () => {
  return (
    <div className="text-center  ">
      <div className="list-group text-capitalize  ">
        <NavLink
          to="/profile/admin"
          className="text-reset text-decoration-none"
        >
          <h4 className="py-2 bg-warning ">Admin Panel</h4>
        </NavLink>

        <NavLink
          to="/profile/admin/create-category"
          className="list-group-item list-group-item-action  p-2 fw-bolder "
        >
          Create category
        </NavLink>
        <NavLink
          to="/profile/admin/create-product"
          className="list-group-item list-group-item-action p-2 fw-bolder "
        >
          Create product
        </NavLink>
        <NavLink
          to="/profile/admin/products"
          className="list-group-item list-group-item-action p-2 fw-bolder "
        >
          products
        </NavLink>
        <NavLink
          to="/profile/admin/allusers"
          className="list-group-item list-group-item-action p-2 fw-bolder "
        >
          users
        </NavLink>
        <NavLink
          to="/profile/admin/allorders"
          className="list-group-item list-group-item-action p-2 fw-bolder "
        >
          orders
        </NavLink>
        <NavLink
          to="/profile/admin/feedback"
          className="list-group-item list-group-item-action p-2 fw-bolder "
        >
          Feedbacks
        </NavLink>
      </div>
    </div>
  );
};

export default AdminMenu;
